main > .content {
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
  max-width: 425px;
}

.number {
  font-size: calc(4vw + 4vh);
  color: var(--color-blue);
}

label {
  font-weight: bold;
  width: 20%;
}
label::after {
  content: ': ';
}

.input-group {
  margin-top: 0.5em;
}

.input-group span {
  font-weight: bold;
}

.input-group button {
  padding: 0.5em;
  width: 8em;
  margin: 0 0.5em;
}

select,
option {
  width: 80%;
  padding: 0.5em;
  text-align: right;
}

textarea {
  width: 90%;
  font-family: inherit;
  font-size: inherit;
}

.result {
  margin: 0.5em 0;
  padding: 0.5em;
  font-weight: bold;
  color: var(--color-white);
}

.result-correct {
  border: 1em solid var(--color-success);
  background: var(--color-success);
}
.result-wrong {
  border: 1em solid var(--color-warning);
  background: var(--color-warning);
}
